import React from 'react';

class Button extends React.Component {
  
  onClick = (e) => {
    console.log(e.nativeEvent.type);
    this.props.onClick(this.props.buttonNum);
  };

 
  render(){
    const {xPos, yPos} = this.props;
    return(
        <rect width="120" height="120" stroke="black" strokeWidth="4" fill="burlywood" y={yPos} x={xPos} onTouchStart={this.onClick} onClick={this.onClick}/>
    );
  }
}

export default Button;


